<template>
  <!-- brainImages 組件 - 腦波圖組件 -->
  <div v-if="hasBrainData" class="brain-images">
    <v-card class="brain-images-card" elevation="2">
      <v-card-title class="brain-images-title">
        <v-icon class="mr-2" color="purple">mdi-brain</v-icon>
        腦波測量結果
      </v-card-title>
      
      <v-card-text class="brain-images-content">
        <div v-for="brainData in brainImagesData" :key="brainData.date" class="brain-image-item">
          <div class="brain-image-header">
            <span class="brain-image-date">{{ brainData.date }}</span>
            <span v-if="brainData.score" class="brain-image-score">
              分數: {{ brainData.score }}
            </span>
          </div>
          
          <div class="brain-image-container">
            <v-img
              :src="brainData.imagePath"
              :alt="`腦波圖 - ${brainData.date}`"
              class="brain-image"
              :aspect-ratio="16/9"
              cover
              @error="handleImageError"
            >
              <template v-slot:placeholder>
                <div class="d-flex align-center justify-center fill-height">
                  <v-progress-circular
                    color="purple"
                    indeterminate
                  ></v-progress-circular>
                </div>
              </template>
            </v-img>
          </div>
        </div>
        
        <!-- 當沒有腦波圖片時的提示 -->
        <div v-if="brainImagesData.length === 0" class="no-brain-data">
          <v-icon size="48" color="grey-lighten-1">mdi-image-off</v-icon>
          <p class="text-grey-lighten-1 mt-2">目前沒有腦波圖片資料</p>
        </div>
      </v-card-text>
    </v-card>
  </div>
</template>

<script setup>
// Props
const props = defineProps({
  // API 響應資料
  response: {
    type: Object,
    default: () => ({
      userid: '',
      datas: []
    })
  },
  // 選中的日期陣列
  selectedDates: {
    type: Array,
    default: () => []
  }
})

// 計算腦波圖片資料
const brainImagesData = computed(() => {
  if (!props.response?.datas) {
    return []
  }
  
  // 過濾出有腦波圖片的資料
  const brainData = props.response.datas
    .filter(item => {
      // 檢查是否有腦波圖片路徑
      const hasImage = item.腦波圖 && item.腦波圖.trim() !== ''
      // 如果有選中日期，只顯示選中日期的資料
      const isSelectedDate = props.selectedDates.length === 0 || props.selectedDates.includes(item.date)
      return hasImage && isSelectedDate
    })
    .map(item => ({
      date: item.date,
      imagePath: item.腦波圖,
      score: item.indicators?.自動化 || null
    }))
    .sort((a, b) => new Date(b.date) - new Date(a.date)) // 按日期降序排列
  
  return brainData
})

// 檢查是否有腦波資料
const hasBrainData = computed(() => {
  return brainImagesData.value.length > 0
})

// 處理圖片載入錯誤
const handleImageError = (event) => {
  console.warn('腦波圖片載入失敗:', event.target.src)
  // 可以在這裡設置預設圖片或顯示錯誤訊息
  event.target.style.display = 'none'
}
</script>

<style scoped>
.brain-images {
  width: 100%;
}

.brain-images-card {
  border-radius: 12px;
  overflow: hidden;
}

.brain-images-title {
  background: linear-gradient(135deg, #9C27B0 0%, #673AB7 100%);
  color: white;
  font-weight: 600;
  padding: 16px 20px;
}

.brain-images-content {
  padding: 20px;
}

.brain-image-item {
  margin-bottom: 24px;
}

.brain-image-item:last-child {
  margin-bottom: 0;
}

.brain-image-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
  padding: 8px 12px;
  background-color: #f5f5f5;
  border-radius: 8px;
}

.brain-image-date {
  font-weight: 600;
  color: #333;
  font-size: 14px;
}

.brain-image-score {
  font-weight: 500;
  color: #9C27B0;
  font-size: 14px;
}

.brain-image-container {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.brain-image {
  width: 100%;
  transition: transform 0.3s ease;
}

.brain-image:hover {
  transform: scale(1.02);
}

.no-brain-data {
  text-align: center;
  padding: 40px 20px;
}

/* 響應式設計 */
@media (max-width: 768px) {
  .brain-images-content {
    padding: 16px;
  }
  
  .brain-image-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 4px;
  }
  
  .brain-image-score {
    align-self: flex-end;
  }
}

@media (max-width: 480px) {
  .brain-images-title {
    padding: 12px 16px;
    font-size: 16px;
  }
  
  .brain-images-content {
    padding: 12px;
  }
  
  .brain-image-item {
    margin-bottom: 16px;
  }
}
</style>