<template>
  <!--等高 style="align-items: flex-start;" -->
  <div class="d-flex ga-2">
    <div class="right-up">最近測得日期{{ latestDatas.date }}</div>
    <skillBlock skill-name="基礎" />
    <skillBlock skill-name="身心" />
    <skillBlock skill-name="認知" />
  </div>
</template>

<script setup>
import skillBlock from './skillBlock.vue'

const latestDatas = inject('latestDatas')

// 檢查腦波資料是否存在
const hasBrainData = computed(() => {
  if (!latestDatas.datas || latestDatas.datas.length === 0) {
    return false
  }

  // 檢查是否有任何一筆資料包含腦波相關資訊
  return latestDatas.datas.some(data => {
    return data.自動化 !== undefined || data.腦波圖
  })
})
</script>
