<template>
  <div class="mt-8">
    <h3>各向度趨勢分析</h3>
    <div ref="chartContainer" class="chart-container"></div>
  </div>
</template>

<script setup>
import * as echarts from 'echarts'

const responsePsychology = inject('responsePsychology')

const chartContainer = ref(null)
let chartInstance = null

const hasData = computed(() => {
  return responsePsychology.value && responsePsychology.value.length > 0
})

// 獲取所有指標名稱
const getIndicatorNames = () => {
  if (!responsePsychology.value || responsePsychology.value.length === 0) {
    return []
  }
  // 排除 date 欄位，取得所有指標名稱
  return Object.keys(responsePsychology.value[0]).filter(key => key !== 'date')
}

// 格式化時間序列數據
const formatTrendData = () => {
  if (!responsePsychology.value || responsePsychology.value.length === 0) {
    return []
  }
  const indicators = getIndicatorNames()

  // 按日期排序數據
  const sortedData = [...responsePsychology.value].sort((a, b) => new Date(a.date) - new Date(b.date))

  // 為每個指標創建數據系列
  return indicators.map((indicator, index) => {
    const data = sortedData.map(item => [
      item.date,
      item[indicator] || 0
    ])

    const color = CHART_COLORS[index % CHART_COLORS.length]

    return {
      name: indicator,
      type: 'line',
      data: data,
      smooth: true,
      symbol: 'circle',
      symbolSize: 6,
      lineStyle: {
        width: 2,
        color: color
      },
      itemStyle: {
        color: color
      }
    }
  })
}

// 初始化圖表
const initChart = () => {
  if (!chartContainer.value) return

  chartInstance = echarts.init(chartContainer.value)
  updateChart()
}

// 更新圖表
const updateChart = () => {
  if (!chartInstance) {
    console.log('Chart instance not initialized');
    return;
  }

  // 防止無數據時觸發渲染
  if (!hasData.value) {
    console.log('No Data');
    chartInstance.clear();
    chartInstance.setOption({
      title: {
        text: 'No Data',
        left: 'center',
        top: 'middle',
        textStyle: {
          color: '#999',
          fontSize: 20
        }
      }
    });
    return;
  }

  const series = formatTrendData();
  const indicators = getIndicatorNames();

  const option = {
    tooltip: {
      trigger: 'axis',
      formatter: function (params) {
        if (!params || params.length === 0) return ''

        const date = params[0].axisValue
        let content = `<strong>${date}</strong><br/>`

        params.forEach(param => {
          const { seriesName, value, color } = param
          const score = value[1]
          content += `<span style="display: inline-block; width: 10px; height: 10px; background-color: ${color}; border-radius: 50%; margin-right: 5px;"></span>${seriesName}: <strong>${score}</strong><br/>`
        })

        return content
      }
    },
    legend: {
      data: indicators,
      top: 0,
      right: 20
    },
    grid: {
      left: 20,
      right: 20,
      bottom: 40,
      top: 66
    },
    xAxis: {
      type: 'category',
      boundaryGap: false,
      axisLine: {
        lineStyle: {
          color: '#999'
        }
      },
      axisLabel: {
        color: '#666'
      },
      splitLine: {
        show: true,
        lineStyle: {
          color: '#e5e5e5',
          type: 'dashed'
        }
      }
    },
    yAxis: {
      type: 'value',
      min: 0,
      max: 5,
      interval: 1,
      axisLine: {
        show: false
      },
      axisLabel: {
        color: '#666',
        fontSize: 16
      },
      splitLine: {
        lineStyle: {
          color: '#e5e5e5',
          type: 'dashed'
        }
      }
    },
    series: series
  };

  chartInstance.setOption(option, true);
}

watch(responsePsychology.value, () => {
  if (chartInstance && hasData.value) {
    updateChart()
  }
}, { deep: true })

// 生命週期
onMounted(() => {
  nextTick(() => {
    initChart()
  })

  // 監聽窗口大小變化
  const handleResize = () => {
    if (chartInstance) {
      chartInstance.resize()
    }
  }
  window.addEventListener('resize', handleResize)

  onUnmounted(() => {
    if (chartInstance) {
      chartInstance.dispose()
      chartInstance = null
    }
    window.removeEventListener('resize', handleResize)
  })
})
</script>

<style scoped>
.chart-container {
  width: 100%;
  height: 320px;
}
</style>