<template>
  <SearchBar @search="handleSearch">
    <template #icon>
      <v-icon>mdi-mother-heart</v-icon>
    </template>
    <template #title>
      心理監控<v-icon class="text-h6" @click="showInfoDialog = true" style="cursor: pointer;">mdi-alert-circle-outline</v-icon>
    </template>

    <template #dashboardTypes>
      <select class="select">
        <option>疲勞監控與身體組成分析</option>
      </select>
    </template>
  </SearchBar>

  <section class="flex pa-4" v-if="responsePsychology?.length > 0">
    <v-row>
      <v-col cols="6">
        <div class="box bg-white">
          <h3>關鍵指標分數</h3>
          <IndicatorContainer />
          <TrendChart />
        </div>
      </v-col>

      <v-col cols="6">
        <div class="box bg-white">
          <RadarChart />
          <BrainImages v-if="brainData" />
          <RecommendationPanel />
        </div>
      </v-col>
    </v-row>
  </section>

  <NoData v-else :message="queryNoDataText" />

  <Desc v-model="showInfoDialog" />
</template>

<script setup>
import SearchBar from '../components/searchBar.vue'
import IndicatorContainer from './components/indicatorContainer.vue'
import RadarChart from './components/radarChart.vue'
import BrainImages from './components/brainImages.vue'
import TrendChart from './components/trendChart.vue'
import RecommendationPanel from './components/recommendationPanel.vue'
import NoData from '../components/noData.vue'
import Desc from './components/desc.vue'
import { dashboard } from "@/requests";

const selectedUserId = ref('')
const allDates = ref([])
const queryNoDataText = ref('')
const responsePsychology = ref([])
const showInfoDialog = ref(false)

const latestDatas = computed(() => {
  if (responsePsychology.value.length === 0) return {}

  // 取得最新日期的數據
  const sortedData = [...responsePsychology.value].sort((a, b) => new Date(b.date) - new Date(a.date))
  return sortedData[0] || {}
})

// 計算屬性 - 腦波數據
const brainData = computed(() => {
  if (responsePsychology.value.length === 0) return null

  // 尋找有腦波圖的最新數據
  const dataWithBrain = responsePsychology.value.find(data => data.腦波圖)
  if (!dataWithBrain) return null

  return {
    date: dataWithBrain.date,
    score: dataWithBrain.自動化 || null,
    imagePath: dataWithBrain.腦波圖
  }
})

const fetchData = async (queryString) => {
  try {
    const result = await dashboard.getPsychologyIndicator(selectedUserId.value, queryString)
    if (result.data) {
      responsePsychology.value = result.data
    } else {
      console.error('API 回傳資料格式錯誤:', result)
      responsePsychology.value = []
    }
  } catch (error) {
    console.error('獲取數據失敗:', error)
    responsePsychology.value = []
  }
}

const handleSearch = async (searchParams) => {
  const queryString = searchParams.queryString
  selectedUserId.value = searchParams.selectedUserId
  await fetchData(queryString)
  
  if (responsePsychology.value.length > 0) {
    queryNoDataText.value = ``
    allDates.value = responsePsychology.value
      .map(data => data.date)
      .sort((a, b) => new Date(b) - new Date(a))
  }
  else
    queryNoDataText.value = `${searchParams.selectedUserName}在所選日期範圍內${searchParams.sDate}~${searchParams.eDate}無資料。`
}


provide('latestDatas', latestDatas)
provide('allDates', allDates)
provide('responsePsychology', responsePsychology)
provide('brainData', brainData)
</script>
