<template>
  <div class="mt-8 position-relative">
    <h3>心理指標建議</h3>
    <v-select v-model="selectedDate" :items="allDates" label="選擇查看日期" variant="outlined" density="compact"
      prepend-inner-icon="mdi-calendar" max-width="180" class=" right-up" hide-details="" style="top:-1px;" />

    <!-- 整體建議區域（基於平均分數） -->
    <v-alert v-if="selectedDate && averageScore !== null" prominent class="mt-6"
      :style="{ backgroundColor: averageScore < 3.6 ? 'rgba(255, 235, 59, .3)' : 'rgba(76, 175, 80, .6)' }">
      <p v-if="averageScore < 3.6" class="text-body-1 mb-2">
        <strong>建議改善方向：</strong>
      </p>
      <p v-else class="text-body-1 mb-2">
        <strong>保持優秀表現：</strong>
      </p>
      <ul class="pl-6">
        <li v-for="(msg, index) in overallRecommendations" :key="index" class="mb-2">
          {{ msg }}
        </li>
      </ul>
    </v-alert>

    <v-alert v-else type="info" variant="outlined" class="text-center">
      請選擇日期以查看建議資訊
    </v-alert>
  </div>
</template>

<script setup>
const responsePsychology = inject('responsePsychology')
const allDates = inject('allDates')

const selectedDate = ref('')


// 計算選定日期的平均分數
const averageScore = computed(() => {
  if (!selectedDate.value || !responsePsychology.value) return null

  const selectedData = responsePsychology.value.find(data => data.date === selectedDate.value)
  if (!selectedData) return null

  // 排除「date」和「自動化」欄位
  const scores = Object.entries(selectedData)
    .filter(([key]) => key !== 'date' && key !== '自動化')
    .map(([, score]) => score)

  if (scores.length === 0) return null

  const sum = scores.reduce((acc, score) => acc + score, 0)
  return sum / scores.length
})

// 計算整體建議內容
const overallRecommendations = computed(() => {
  if (averageScore.value === null) return []

  if (averageScore.value < 3.6) {
    return [
      '請檢視你的微目標(Process Goals)，專注於「今天能做到的事」，而非最終結果，能有效建立信心和掌控感。',
      '練習傾聽身體的聲音與放鬆技巧，不僅是看見，還要感覺、聽見成功的技術動作，強化肌肉與心靈的連結。',
      '運用有力量的自我對話，例如：「我只有現在」、「Nice」，在突發狀況下，中斷負面思維的循環。建立例行動作幫助自己從比賽結果轉向任務執行過程。'
    ]
  } else {
    return [
      '充滿意義的生活，繼續保持自信跟積極的投入，也要記得休息與心理恢復唷！',
      '已具備良好的情緒調節能力，賽前調整很有一套唷！',
      '頭腦清楚，戰術明確，專注於致勝關鍵的行動！'
    ]
  }
})

// 監聽可用日期變化，自動選擇最新日期
watch(allDates, (newDates) => {
  if (newDates.length > 0) {
    selectedDate.value = newDates[0] // 選擇最新日期
  }
}, { immediate: true })
</script>
